import DeepData from "../../../assets/js/formate.js";
export default {
  name: "GMcontrol",

  data() {
    return {
      orderlist: ["ascending", "descending"],
      dialogFormVisible: false,
      formLabelWidth: "120px",
      tableData: [1],
      currentPage: 1,
      limit: 10,
      total: "",
      title: "添加控制名单",
      opationStatus: [
        { value: 2, label: "全部" },
        { value: 0, label: "终止" },
        { value: 1, label: "执行" },
      ],
      opationAmount: [
        { value: 1, label: "全部" },
        { value: 2, label: "正数" },
        { value: 3, label: "负数" },
      ],
      format: {
        play_id: "",
        status: "全部",
        control_money: "全部"
      },

      form: {
        name: ""
      },
      rules: {
        name: [
          {
            required: true,
            message: "必填项不可以为空,只能输入数字",
            trigger: "blur"
          }
        ]
      },
      tableDialog:[],
      visible:false,
    };
  },
  created() {
    this.initData()
  },
  methods: {
    /**搜索*/
    add() {
      this.dialogFormVisible = true;
      this.title = "添加控制名单";
    },
    search() {},
    //表格停止
    handleStop() {

      //停止接口
    },

    //表格编辑
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogFormVisible = true;
      this.title = "编辑";
    },

    //表格详情
    handleDetail() {
      this.visible = true
    },

    //页容量变化
    handleSizeChange(num) {
      this.limit = num;
      this.currentPage = 1;
      this.initData()
    },

    //页码变化
    handleCurrentChange(pagenum) {
      this.currentPage = pagenum
      this.initData()
    },

    //表单提交
    onSubmit(formName) {
      this.$refs[formName].validate(async (valid, x) => {
        if (valid) {
          //提交请求
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    GetData(){
      this.initData({
        page:this.currentPage,
        limit:this.limit,
        uid:this.format.play_id,
        status:this.format.status,
        amount	:this.format.control_money,
      })
    },

    formatData(res){
      res.forEach((item)=>{
        item.curr_status = item.curr_status === 1 ? '启用' : '终止'
      })
      return res
    },

    async initData(params){
      let {data} = await this.$http.OperationMan.GetGameControl(params)
      console.log(data);
      let res = DeepData(data.data)
      console.log(res);
      
      this.tableData =this.formatData(res);
      this.total = data.total
      
    },

    // handleDelete(index, row) {
    //   console.log(row);
    //   const ids = row.id.toString();
    //   console.log(ids);
    //   this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
    //     confirmButtonText: "确定",
    //     cancelButtonText: "取消",
    //     type: "warning"
    //   })
    //     .then(
    //       this.$message({
    //         type: "success",
    //         message: res.data.msg
    //       })
    //     )
    //     .catch(() => {
    //       this.$message({
    //         type: "info",
    //         message: res.data.msg
    //       });
    //     });
    // }
  }
};



